/* ==============================================================================
// Custom buttons
// ============================================================================== */
.button {
  -webkit-font-smoothing: antialiased;
  border: 0;
  outline: none;
  padding: 1px;
  display: inline-block;
  text-decoration: none;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px;
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.075);
  -moz-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.075);
  -ms-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.075);
  -o-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.075);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.075);
  height: 33px;
  background: #adb2bb;
  background: -webkit-linear-gradient(#ccd0d6, #adb2bb);
  background: -moz-linear-gradient(#ccd0d6, #adb2bb);
  background: -ms-linear-gradient(#ccd0d6, #adb2bb);
  background: -o-linear-gradient(#ccd0d6, #adb2bb);
  background: linear-gradient(#ccd0d6, #adb2bb);
  -webkit-transition: all 0.1s linear;
  -moz-transition: all 0.1s linear;
  -ms-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
  -moz-transform: scale3d(1, 1, 1);
  -o-transform: scale3d(1, 1, 1);
  -ms-transform: scale3d(1, 1, 1);
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
}

.button[disabled], .button.disabled {
  pointer-events: none;
  cursor: not-allowed;
  box-shadow: none;
}

.button[disabled] span, .button.disabled span {
  box-shadow: none;
  background: #E7EBF0;
  background: #E7EBF0;
  background: -webkit-linear-gradient(#E7EBF0, #E7EBF0);
  background: -moz-linear-gradient(#E7EBF0, #E7EBF0);
  background: -ms-linear-gradient(#E7EBF0, #E7EBF0);
  background: -o-linear-gradient(#E7EBF0, #E7EBF0);
  background: linear-gradient(#E7EBF0, #E7EBF0);
}

.button:hover {
  text-decoration: none;
}

.button:active, .button.active {
  background: #b5bac2;
  background: -webkit-linear-gradient(#b5bac2, #b5bac2);
  background: -moz-linear-gradient(#b5bac2, #b5bac2);
  background: -ms-linear-gradient(#b5bac2, #b5bac2);
  background: -o-linear-gradient(#b5bac2, #b5bac2);
  background: linear-gradient(#b5bac2, #b5bac2);
  -moz-transform: scale3d(0.95, 0.95, 0.95);
  -o-transform: scale3d(0.95, 0.95, 0.95);
  -ms-transform: scale3d(0.95, 0.95, 0.95);
  -webkit-transform: scale3d(0.95, 0.95, 0.95);
  transform: scale3d(0.95, 0.95, 0.95);
}

.button:active span,
.button:active span, .button.active span,
.button.active span {
  color: #444;
  background: #eff1f4;
  background: -webkit-linear-gradient(#e6e8eb, #eff1f4);
  background: -moz-linear-gradient(#e6e8eb, #eff1f4);
  background: -ms-linear-gradient(#e6e8eb, #eff1f4);
  background: -o-linear-gradient(#e6e8eb, #eff1f4);
  background: linear-gradient(#e6e8eb, #eff1f4);
  -webkit-box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
  -ms-box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
  -o-box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
}

.button span {
  display: block;
  font-size: 14px;
  padding-left: 15px;
  padding-right: 15px;
  height: 31px;
  line-height: 31px;
  border-radius: 4px;
  font-weight: bold;
  font-family: "Helvetica Neue", Arial;
  color: #333;
  text-shadow: 0 1px 0 #fff;
  background: #f6f9fc;
  background: #eff1f4;
  background: -webkit-linear-gradient(#f7f8fa, #eff1f4);
  background: -moz-linear-gradient(#f7f8fa, #eff1f4);
  background: -ms-linear-gradient(#f7f8fa, #eff1f4);
  background: -o-linear-gradient(#f7f8fa, #eff1f4);
  background: linear-gradient(#f7f8fa, #eff1f4);
  -webkit-box-shadow: inset 0 1px 0 white;
  -moz-box-shadow: inset 0 1px 0 white;
  -ms-box-shadow: inset 0 1px 0 white;
  -o-box-shadow: inset 0 1px 0 white;
  box-shadow: inset 0 1px 0 white;
  -webkit-transition: all 0.1s linear;
  -moz-transition: all 0.1s linear;
  -ms-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
}

