/* 
	Create a custom select by adding a [data-smart-select]
	to any select. e.g. <select class="form-control" data-smart-select>
	and it will automatically be converted into a customized select
	that is wrapped inside a .fake-select-wrap
*/
.fake-select-wrap {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 34px;
  line-height: 30px;
  margin: 0;
}

.fake-select-wrap > select {
  position: absolute !important;
  z-index: 3;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  width: 100% !important;
  height: 100% !important;
  cursor: pointer;
}

.fake-select-wrap > .fake-select {
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 4px;
  width: 100%;
  margin: 0;
  vertical-align: bottom;
  padding: 0 10px;
  white-space: nowrap;
  padding-right: 20px;
  text-overflow: ellipsis;
  /*overflow: hidden;*/
  border: 1px solid #ccc;
  background: #fff;
  height: 34px;
  line-height: 30px;
  cursor: pointer;
  color: #444;
}

.fake-select-wrap > .fake-select.focus {
  border-color: #98c7f8;
}

.fake-select-wrap > .fake-select.focus:after {
  color: #81B5EC;
}

.fake-select-wrap > .fake-select:after {
  font-family: 'FontAwesome';
  font-size: 13px;
  content: "\f078";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  color: #757575;
  display: block;
  position: absolute;
  right: 13px;
  height: 100%;
  top: 17px;
  line-height: 0;
}

/* ==============================================================================
// jQuery Validate messages
// ============================================================================== */
form label.error {
  position: relative !important;
  display: inline-block !important;
  margin-top: -2px !important;
  margin-bottom: -8px !important;
  color: #fff !important;
  font-weight: normal !important;
  font-size: 14px !important;
  padding: 5px 8px !important;
  background: #589BC9 !important;
  border-radius: 4px !important;
}

form label.error.valid {
  display: none !important;
}

form label.error:after {
  bottom: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-bottom-color: #589BC9;
  border-width: 5px;
  left: 30px;
  margin-left: -5px;
}

